'use client';

import Image from 'next/image';
import Link from 'next/link';
import React, { useState } from 'react';
import { Menu, X } from 'lucide-react';
import { ASSETS } from '../../lib/constants';

const NAV_LINKS = [
  { label: 'home', href: '#hero' },
  { label: 'manifesto', href: '/manifesto' },
  { label: 'sobre', href: '/sobre' },
  { label: 'portfolio showcase', href: '/portfolio' },
  { label: 'contato', href: '#contact' },
];

const Header: React.FC = () => {
  const [isMobileOpen, setIsMobileOpen] = useState(false);

  return (
    <header className="fixed inset-x-0 top-0 z-50 border-b border-white/40 bg-white/90 backdrop-blur-xl">
      <div className="mx-auto flex max-w-6xl items-center justify-between gap-4 px-4 py-3 sm:px-6">
        <Link
          href="/"
          className="flex items-center gap-3 text-[#111111]"
          onClick={() => setIsMobileOpen(false)}
        >
          <span className="relative h-10 w-10">
            <Image
              src={ASSETS.logoDark}
              alt="Danilo Novais — marca"
              fill
              priority
              sizes="40px"
              className="object-contain"
            />
          </span>
          <span className="hidden text-sm font-semibold tracking-[0.6em] uppercase lg:block">
            danilo
          </span>
        </Link>

        <nav className="hidden items-center gap-3 text-xs font-semibold uppercase tracking-[0.4em] text-[#111111] md:flex">
          {NAV_LINKS.map((link) => (
            <Link
              key={link.label}
              href={link.href}
              onClick={() => setIsMobileOpen(false)}
              className="rounded-full px-3 py-1 transition-colors duration-200 hover:bg-[#0057FF]/10 hover:text-[#0057FF]"
            >
              {link.label}
            </Link>
          ))}
        </nav>

        <button
          onClick={() => setIsMobileOpen((prev) => !prev)}
          className="text-[#111111] focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-[#0057FF] rounded-full md:hidden"
          aria-label={isMobileOpen ? 'Fechar menu' : 'Abrir menu'}
          aria-expanded={isMobileOpen}
        >
          {isMobileOpen ? <X size={24} /> : <Menu size={24} />}
        </button>
      </div>

      {isMobileOpen && (
        <div className="md:hidden border-t border-white/30 bg-white/95 px-4 py-6">
          <nav className="flex flex-col gap-4 text-sm font-semibold uppercase tracking-[0.4em]">
            {NAV_LINKS.map((link) => (
              <Link
                key={link.label}
                href={link.href}
                onClick={() => setIsMobileOpen(false)}
                className="block rounded-full px-4 py-2 text-center transition-colors duration-200 hover:bg-[#0057FF]/10 hover:text-[#0057FF]"
              >
                {link.label}
              </Link>
            ))}
          </nav>
        </div>
      )}
    </header>
  );
};

export default Header;
